/* @group 1. Main Styles */

body { 
  color: #000; 
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
  font-family: 'Work Sans';
  background: #fff;
}

  .fixed-body {
    padding: 0px 20px;
  }

* {
     outline: 0;
}

b, 
strong {
  font-weight: 600;
}

a { 
     cursor: pointer;
  color:#000;
  line-height:1.6; 
  transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
}

a:hover {
  color: #16b2e1;
  text-decoration:none;
}

#main {
     overflow: hidden;
}

.container .container {
  width: auto !important;
}

.standard-body .full-width .container {
  max-width: 1280px;
  margin: 0px auto;
  padding-left: 70px;
  padding-right: 70px;
}
  .standard-body .full-width .container .container {
    padding: 0px;
  }
  
  .standard-body .full-width .shadow {
    display: none;
  }

.standard-body .fixed {
  padding: 0px 20px;
}

  .standard-body .fixed .background {
    margin: 0px auto;
    max-width: 1240px;
    position: relative;
  }
  
       .standard-body .fixed .background .background {
            width: auto !important;
       }

  .standard-body .fixed .container {
    padding-left: 50px;
    padding-right: 50px;
    margin: 0px;
    max-width: 100% !important;
  }
    .standard-body .fixed .container .container {
      padding: 0px;
    }
    
.main-fixed {
  margin: 0px auto;
  max-width: 1240px;
  background: #fff;
  box-shadow:         0px 0px 25px rgba(0, 0, 0, 0.17);
}

  .main-fixed .fixed {
    padding: 0px !important;
  }
    
    .main-fixed .background .shadow {
      display: none !important;
    }
    
.fixed-body .container {
  padding-left: 50px;
  padding-right: 50px;
  max-width: none;
}

  .fixed-body .container .container {
    padding: 0px;
  }
  
/* @end */
  
/* @group 2. Inputs
-------------------*/

textarea, 
input[type="text"], 
input[type="password"], 
input[type="datetime"], 
input[type="datetime-local"], 
input[type="date"], 
input[type="month"], 
input[type="time"], 
input[type="week"], 
input[type="number"], 
input[type="email"], 
input[type="url"], 
input[type="search"], 
input[type="tel"], 
input[type="color"], 
.uneditable-input,
select {
  background: #fff;
  color:#000;
  font-weight: 400;
  font-family: Work sans;
  border: 1px solid #e5e5e5;
  margin: 5px 0px;
  padding: 13px 18px;
  box-shadow:         none;
  -moz-box-shadow:    none;
  -webkit-box-shadow: none;
  border-radius:         3px;
  -moz-border-radius:    3px;
  -webkit-border-radius: 3px;
  -webkit-transition: border linear 0.2s, box-shadow linear 0.2s;
  transition:         border linear 0.2s, box-shadow linear 0.2s;
}

  input#input-captcha {
    margin-bottom: 15px;
  }

  textarea:focus,
  input[type="text"]:focus,
  input[type="password"]:focus,
  input[type="datetime"]:focus,
  input[type="datetime-local"]:focus,
  input[type="date"]:focus,
  input[type="month"]:focus,
  input[type="time"]:focus,
  input[type="week"]:focus,
  input[type="number"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="search"]:focus,
  input[type="tel"]:focus,
  input[type="color"]:focus,
  .uneditable-input:focus {
    border-color: #16b2e1;
  }
  
  textarea {
    max-width: 100%;
  }
  
  select {
    height: 40px;
  }
  
/* @end */
  
/* @group 3. Dropdown 
-------------------*/
  
#main .dropdown-menu {
  display: block;
  opacity: 0;
  filter: alpha(opacity=0);
  visibility: hidden;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  margin-top: 15px;
}

#main .show > .dropdown-menu {
  margin-top: 0px;
  display: block;
  opacity: 1; 
  filter: alpha(opacity=100);
  visibility: visible;
}

.dropdown-menu {
  background: #fff;
  color: #000;
  padding: 14px 0px;
  margin-top: 9px;
  border: none;
  border-radius:         3px;
  -moz-border-radius:    3px;
  -webkit-border-radius: 3px;
  box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.3);
  font-weight: 400;
  font-size: 14px;
  -webkit-transform: none !important;
          transform: none !important;
  top: 100% !important;
}

  .dropdown-menu a {
    color: #000;
    font-weight: 400;
  } 
    
    .dropdown-menu > li > a {
      display: block;
      padding: 3px 20px;
      clear: both;
      line-height: 1.428571429;
      white-space: nowrap;
    }

    .dropdown-menu > li > a:hover,
    .dropdown-menu > li > a:focus {
      background: #f7f7f7;
    }
    
  .nav-header {
    color: #000;
  }
  
/* @end */
  
/* @group 4. Buttons 
------------------*/

.button,
.btn {
  display:inline-block;
  cursor:pointer;
  border: none;
  color:#fff !important;
  font-size: 13px;
  font-weight: 600 !important;
  padding:11px 28px 9px 28px;
  border-radius:         3px;
  -moz-border-radius:    3px;
  -webkit-border-radius: 3px;
  background: #16b2e1;
  outline: none;
}

  .button:hover,
  .btn:hover {
    color: #fff;
    background: #14a0cb;
  }
  
  .buttons .left .button,
  .buttons .center .button,
  .btn-default,
  .input-group-btn .btn-primary {
    background: #000;
  }
  
       .buttons .left .button:hover,
       .buttons .center .button:hover,
       .btn-default:hover,
       .input-group-btn .btn-primary:hover {
            background: #333;
       }
  
  .input-group input {
    border-right: none;
  }
  
  .input-group-btn .btn {
    height: 40px;
    margin: 5px 0px;
  }

.buttons { 
  overflow: auto;
  margin: 0px 0px 20px 0px; 
}
  
  .buttons .btn {
    margin-top: 10px;
  }
  
  .buttons .left { 
    float: left; 
    text-align: left; 
  }
  
  .buttons .right { 
    float: right;
    text-align: right; 
  }
  
    .buttons .right .button {
      margin-left: 10px;
    }
  
  .buttons .center { 
    text-align: center;
    margin-left: auto;
    margin-right: auto; 
  }
  
.button-continue-shopping {
  display: block;
  float: left;
}

.button-checkout {
  display: block;
  float: right;
}

/* @end */
  
/* @group 5. Header 
------------------*/

header {
  position: relative;
}

     header.fixed {
          background: none !important;
     }
     
     header.fixed .background2 {
          border-radius: 0px 0px 3px 3px;
          -webkit-border-radius: 0px 0px 3px 3px;
          -moz-border-radius: 0px 0px 3px 3px;
     }
     
     header.fixed .megamenu-wrapper,
     header.fixed .megamenuToogle-wrapper {
          background: url(data:image/png;base64,) top left repeat-x;
     }
     
     header.fixed #top-bar .background {
          background: url(data:image/png;base64,) bottom left repeat-x;
          border: none;
          padding: 0px 30px 1px 30px;
     }
     
     header.fixed #top-bar .menu > li:before,
     header.fixed #top-bar form:before {
          background: url(data:image/png;base64,);
     }
     
     header.fixed #top .pattern > .container:first-child {
          padding-left: 30px;
          padding-right: 30px;
     }
     
     header.fixed #top .search_form input {
          border: none;
     }

  header .background-header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -3;
  }
  
  @media (max-width: 991px) and (min-width: 767px) {
       #root .standard-body .fixed .background > .container,
       #root .standard-body .fixed .pattern > .container {
            padding-left: 0 !important;
            padding-right: 0 !important;
       }
  }
  
  @media (max-width: 991px) {
       #root header.fixed #top .pattern > .container:first-child,
       #root header.fixed #top-bar .background {
            padding-left: 20px !important;
            padding-right: 20px !important;
       }
       
       #root header.fixed .megamenu-wrapper {
            border: none;
            margin-top: 0;
            border-radius: 0px 0px 3px 3px;
            -webkit-border-radius: 0px 0px 3px 3px;
            -moz-border-radius: 0px 0px 3px 3px;
            padding-top: 0;
       }
  }
  
/* @end */

/* @group 6. Top Bar
------------------*/

#top-bar {
  position: relative;
}

#top-bar .background-top-bar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

#top-bar .background {
  background: #f2f2f2;
  border-bottom: 1px solid #dadada;
}

#top-bar .container {
  font-size: 12px;
  font-weight: 500;
}
  
  #top-bar .menu {
       list-style: none;
       padding: 0px;
       margin: 0px;
       text-align: right;
  }
  
       #top-bar .menu > li {
            display: inline-block;
            vertical-align: top;
            margin: 0px 0px 0px 40px;
            padding: 11px 0px 0px 0px;
            text-align: left;
            position: relative;
            height: 39px;
       }
       
            #top-bar .menu > li:before {
                 content: '';
                 position: absolute;
                 top: 0;
                 bottom: 0;
                 width: 1px;
                 background: #dadada;
                 margin-left: -20px;
            }
            
                 #top-bar .menu > li:first-child:before {
                      display: none;
                 }
       
       #top-bar .menu > li .caret {
            border-top-color: #a9a9a9;
       }
       
     #top-bar form {
          float: left;
          padding-right: 40px;
          position: relative;
     }
     
          #top-bar form:before {
               content: '';
               position: absolute;
               top: 0;
               bottom: 0;
               width: 1px;
               background: #dadada;
               margin-left: -20px;
          }
          
               #top-bar form:first-of-type:before {
                    display: none;
               }
     
          #top-bar form .dropdown > a {
               display: block;
               height: 39px;
               padding: 11px 0px 0px 0px;
          }
          
          #top-bar form .caret {
               border-top-color: #a9a9a9;
          }
        
/* @end */

/* @group 7. Top of pages
------------------*/

#top {
  position: relative;
}

  #top .container {
    padding-top: 22px;
    padding-bottom: 34px;
  }
  
    #top .container .container {
      padding-top: 0px;
      padding-bottom: 0px;
    }
  
  #top .background-top {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
  }

  /* @group Logo */
  
  #top .logo {
    padding: 17px 0px 0px 0px;
    margin: 0px;
    line-height: 100%;
    font-size: 100%;
  }
  
       #top .logo img {
            display: inline-block;
       }
  
  /* @end */
  
  /* @group Welcome text */
  
  #top .welcome-text {
    padding: 10px 0px 0px 0px;
  }
  
  /* @end */
  
  /* @group  Search */
  
  #top .search_form {
    margin: 16px 27px 0px -80px;
    padding: 0px;
    display: inline-block;
    position: relative;
    width: 383px;
    vertical-align: top;
  }
  
    #top .search_form input {
      padding: 0px 35px 0px 18px;
      margin: 0px;
      height: 40px;
      width: 100%;
      font-size: 14px;
    }
    
    .search_form .button-search,
    .search_form .button-search2 {
      position: absolute;
      right: 20px;
      top: 11px;
      cursor: pointer;
      font-size: 14px;
      line-height: 14px;
      font-weight: 400;
      color: #000;
    }
      
  /* @end */
      
  /* @group Cart block */
  
  #top #cart_block {
    padding: 16px 0px 0px 0px;
    display: inline-block;
    vertical-align: top;
  }
  
    #top #cart_block .cart-heading {
      
      cursor: pointer;
      background: #f3bb1d;
      padding: 9px 20px 6px 0px;
      border-radius:         3px;
      -moz-border-radius:    3px;
      -webkit-border-radius: 3px;
      height: 40px;
      font-weight: 500;
    }
    
         #top #cart_block .cart-heading i {
              display: inline-block;
              vertical-align: top;
              width: 40px;
              text-align: center;
              font-size: 15px;
              padding-top: 3px;
              margin-right: 16px;
         }
         
              #top #cart_block .cart-heading:after {
                   position: absolute;
                   top: 0;
                   bottom: 0;
                   left: 0;
                   width: 1px;
                   background: rgba(0, 0, 0, .1);
                   margin-left: 40px;
              }
    
    #top #cart_block .dropdown-menu {
      width: 450px;
      text-align: left;
      padding: 0px;
      left: auto !important;
      right: 0 !important;
    }
    
      #main #top #cart_block.show > .dropdown-menu {
        margin-top: 0px;
      }

      #top #cart_block .dropdown-menu:after {
        position: absolute;
        top: -6px;
        right: 90px;
        display: inline-block;
        border-right: 6px solid transparent;
        border-bottom: 6px solid #ffffff;
        border-left: 6px solid transparent;
        content: '';
      }
          
          /* @group Content */
          
          .mini-cart-info table {
            border-collapse: collapse;
            width: 100%;
            margin-bottom: 5px;
          }
          
            .mini-cart-info td {
              padding: 10px;
              border-bottom: 1px solid #e6e6e6;
            }
            
              .mini-cart-info .image {
                padding: 20px 10px 20px 20px;
              }
              
              .mini-cart-info .image img {
                border: 1px solid #EEEEEE;
                display: block;
                max-width: 80px !important;
              }
              
              .mini-cart-info .name {
                padding: 20px 10px;
              }
              .mini-cart-info .quantity {
                text-align: right;
                padding: 20px 10px;
              }
              
              .mini-cart-info td.total {
                text-align: right;
                padding: 20px 10px;
                color: #16b2e1;
                font-weight: 600;
              }
              
              .mini-cart-info .remove {
                text-align: right;
                padding: 10px 20px 13px 10px;
              }
              
                .mini-cart-info .remove img {
                  cursor: pointer;
                }
          
          .mini-cart-total {
            text-align: right;
          }
          
            .mini-cart-total table {
              border-collapse: collapse;
              display: inline-block;
              padding: 20px 13px 10px 13px;
            }
            
              .mini-cart-total td {
                padding: 0px 7px;
              }
              
                   .mini-cart-total td b {
                        font-weight: 400;
                   }
                
                .mini-cart-total td:last-child {
                  color: #16b2e1;
                  font-weight: 600;
                }
          
          #cart_block .checkout {
            text-align: right;
            clear: both;
            padding: 0px 20px 20px 20px;
          }
          
          #cart_block .empty {
            padding: 50px 20px;
            text-align: center;
          }
        
        /* @end */
          
  /* @end */
      
  /* @group Right */
  
  #top #header-right {
    text-align: right;
  }
  
  /* @end */
  
  /* @group Fixed Top */
  
  .is-sticky #top {
    z-index: 99;
    padding: 0px;
    width: 100%;
    left: 0;
    right: 0;
  }
  
    .is-sticky #top .background {
      width: 100%;
      opacity: 0.97;
    }
    
    .is-sticky #top .container {
      padding-top: 0px;
      padding-bottom: 20px;
    }
    
    .is-sticky #top ul.header-links {
      display: none;
    }
    
    .is-sticky #top .logo img {
      max-height: 50px;
    }
    
  /* @end */
    
/* @end */
    
/* @group 8. Breadcrumb
------------------*/

.breadcrumb {
}

    .breadcrumb .container ul {
      list-style: none;
      padding: 14px 0px 15px 0px;
      margin: 0px;
      background: url(data:image/png;base64,) bottom left repeat-x;
    }
    
      .breadcrumb .container ul li {
        display: inline-block;
        padding-right: 4px;
      }
      
        .breadcrumb .container ul li:before {
          content: "›";
          padding-right: 4px;
        }
        
          .breadcrumb .container ul li:first-child:before {
            display: none;
          }
          
/* @end */
          
/* @group 9. Main Content
------------------*/

.main-content .pattern > .container,
.standard-body .main-content .pattern > .container {
  padding-bottom: 60px;
}

  /* @group Banner */
  
  .fashion-home-banners {
       position: relative;
       margin-top: -120px;
       z-index: 99;
  }
  
       @media (max-width: 991px) {
            #root .fashion-home-banners {
                 margin-top: 0;
            }
       }
  
  .banners > div {
    padding-top: 30px;
  }
  
       .banners.banners-with-padding-40 > div {
            padding-top: 40px;
       }
  
    .banners > div img {
      display: block;
      margin: 0px auto;
    }
    
  /* @end */
  
  /* @group Center column */
  
  .center-column {
    padding-top: 24px;
    position: relative;
    margin-bottom: -20px;
  }
    
    .center-column h2,
    .center-column h3,
    .center-column h4,
    .center-column h5,
    .center-column h6 {
      margin: 0px 0px 10px 0px;
      padding: 10px 0px 0px 0px;
      font-weight: 600;
    }
    
    .center-column h1 {
         margin: 0px 0px 0px 0px;
         padding: 0px 0px 17px 0px;
         font-size: 30px;
         font-weight: 600;
         line-height: 1;
    }
    
         body[class*="product-product-"] .center-column h1 {
              height: 6px;
              padding: 0px;
              text-indent: -1000px;
              overflow: hidden;
         }
    
  /* @end */
  
  /* @group Block */
  
  .box {
    padding-top: 21px;
  }
       
    .mfilter-box.box {
      margin: 0px;
    }

    .box .box-heading {
      float: left;
      margin: 23px 20px 23px 0px;
      padding: 0px;
      font-size: 23px;
      line-height: 23px;
      font-weight: 600;
    }
    
    .box .strip-line {
      background: url(data:image/png;base64,) 0px 50% repeat-x;
      height: 66px;
      overflow: hidden;
    }
    
      .center-column .box {
        padding-bottom: 20px;
        padding-top: 13px;
      }
    
    .box .box-content {
      clear: both;
    }
    
      .box .box-content.products {
        float: left;
        width: 100%;
      }
      
        .col-md-3 .box .box-content.products,
        .col-md-4 .box .box-content.products {
          float: none;
        }
    
    .box > .prev, 
    .box > .next {
      float: right;
      margin: 18px 0px 0px 10px;
      width: 30px;
      height: 30px;
      border-radius:         3px;
      -moz-border-radius:    3px;
      -webkit-border-radius: 3px;
      background: #cccccc;
    }
    
         .box > .prev span, 
         .box > .next span {
              display: block;
              width: 30px;
              height: 30px;
              border-radius:         3px;
              -moz-border-radius:    3px;
              -webkit-border-radius: 3px;
              background: url(data:image/png;base64,) top left no-repeat;
         }
    
         .box > .next span {
              background-image: url(data:image/png;base64,);
         }
    
         .box > .prev:hover, 
         .box > .next:hover {
              background-color: #333333;
         }
      
      .box > .prev {
        margin-left: 20px;
      }
        
  /* @end */
        
  /* @group Block Category */
  
  .box-with-categories {
       padding: 0px !important;
       margin: 30px 0px 0px 0px;
       border: 1px solid #dadada;
       border-radius: 3px;
       -webkit-border-radius: 3px;
       -moz-border-radius: 3px;
  }
  
       .box-with-categories .box-heading {
            float: none;
            padding: 14px 20px 11px 20px;
            margin: 0px;
            background: #f2f2f2;
            border-bottom: 1px solid #dadada;
            border-radius: 3px 3px 0px 0px;
            -moz-border-radius: 3px 3px 0px 0px;
            -webkit-border-radius: 3px 3px 0px 0px;
            font-size: 20px;
       }
  
       .box-with-categories .strip-line {
            display: none;
       }
  
  .box-category {
       padding: 10px 0px;
  }
  
    .box-category ul {
      list-style: none;
      padding: 0px;
      margin: 0px;
    }
    
      .box-category > ul > li {
        border-bottom: 1px solid #e6e6e6;
        padding: 0px;
        font-size: 15px;
        font-weight: 500;
        position: relative;
      }
      
           .box-category > ul > li:last-child {
                border-bottom: none;
           }
      
        .box-category ul li a {
          line-height: 39px;
          display: block;
          padding: 0px 20px;
        }
        
          .box-category ul li a.active {
            color: #16b2e1;
          }
          
          .box-category ul li .head {
               position: absolute;
               top: 0;
               right: 0;
          }
        
          .box-category ul li .head a {
            font-size: 16px;
            line-height: 39px;
            display: block;
            padding: 0px 16px !important;
          }
          
            .box-category ul li .head .plus {
              display: none;
            }
            
              .box-category ul li .head .collapsed .plus {
                display: block;
              }
              
              .box-category ul li .head .collapsed .minus {
                display: none;
              }
      
        .box-category ul li ul li {
