/* @group 20. Camera slider */

.camera_slider {
  margin: 30px 0px 0px 0px;
  position: relative;
}

.camera_wrap {
  margin: 0px !important;
  width: auto;
  direction: ltr;
  display: none;
  position: relative;
  z-index: 1;
}

  .spinner {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 22;
    background: #fff url(data:image/gif;base64,) 50% 50% no-repeat;
  }

  #slider .fullwidth .camera_slider,
  #slider .fullwidth .camera_slider .camera_wrap {
    margin: 0px !important;
    padding: 0px;
  }
  
  .camera_wrap img {
    display: block;
    margin: 0px auto;
  }

    .camera_wrap .owl-nav {
      opacity: 0;
      filter: alpha(opacity=0);
      -webkit-transition: all 0.4s;
      transition: all 0.4s;
      visibility: hidden;
    }
    
      .camera_wrap:hover .owl-nav {
        visibility: visible;
        opacity: 1;
        filter: alpha(opacity=100);
      }
    
      .camera_wrap .owl-nav .owl-prev,
      .camera_wrap .owl-nav .owl-next {
        position: absolute;
        left: -25px;
        top: 50%;
        margin: -20px 0px 0px 0px !important;
        height: 40px;
        width: 40px;
        margin: 0px;
        text-indent: -10000px;
        background: url(data:image/png;base64,) 5px 5px no-repeat;
        background-color: rgba(0, 0, 0, .2);
        display: block;
        border-radius: 3px;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        transition: all 0.2s ease-in;
        -webkit-transition: all 0.2s ease-in;
        -moz-transition: all 0.2s ease-in;
      }
      
           .camera_wrap:hover .owl-nav .owl-prev {
                left: -10px;
           }
      
        .camera_wrap .owl-nav .owl-next {
          left: auto;
          right: -25px;
          background: url(data:image/png;base64,) 5px 5px no-repeat;
          background-color: rgba(0, 0, 0, .2);
        }
        
             .camera_wrap:hover .owl-nav .owl-next {
                  right: -10px;
             }
        
        .camera_wrap .owl-nav .owl-prev:hover,
        .camera_wrap .owl-nav .owl-next:hover {
             background-color: rgba(0, 0, 0, .8);
        }
      
    .camera_wrap .owl-nav .owl-pagination {
      display: none;
    }
          
