/* // COUNTER /////////////////////////////////////////////////////////////// */
.mfilter-counter {
	border-radius: 3px;
	background: #428BCA;
	color: #fff;
	padding: 0 4px;
	float: right;
	line-height: 20px;
	position: relative;
	margin-left: 4px;
	outline: 0 !important;
}
.mfilter-counter:after {
	right: 100%;
	top: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
	border-color: rgba(136, 183, 213, 0);
	border-right-color: #428BCA;
	border-width: 4px;
	margin-top: -4px;
}
